import React from "react"
import { useStaticQuery, Link, graphql, withPrefix } from "gatsby"
import Img from "gatsby-image"
import PropTypes from "prop-types"
import 'bootstrap/dist/css/bootstrap.css';
import { Container, Row, Col, Navbar, NavDropdown, Nav } from "react-bootstrap"
import "./app.css"

const Header = () => {
  const data = useStaticQuery(graphql`
    query {
      file(relativePath: { eq: "logo-dark3.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <header role="banner">
      <nav role="navigation" aria-label="Main Navigation">
        <Navbar collapseOnSelect expand="lg">
          <Navbar.Brand href="/"><Img fluid={data.file.childImageSharp.fluid} alt="" className="header-logo" alt="logo"/></Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto">
            </Nav>
            <Nav>
              <Nav.Link className="link navlink first-navlink" href="/">work</Nav.Link>
              <Nav.Link eventKey={2} className="link navlink" href="/about">about</Nav.Link>
              <Nav.Link eventKey={3} className="link navlink" href="/resume">resume</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </nav>  
    </header>
  )
}

export default Header