import React from "react"
import Anime from 'react-anime'
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import 'bootstrap/dist/css/bootstrap.css';
import { Container, Row, Col } from "react-bootstrap"
import "../components/app.css"
import Layout from "../components/layout.js"
import SEO from "../components/seo"
import ProjectNav from "../components/project-nav"

const CommunityPage = () => {
  const data = useStaticQuery(graphql`
    query {
      imageOne: file(relativePath: { eq: "home/community.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageTwo: file(relativePath: { eq: "community/community_affin1.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageThree: file(relativePath: { eq: "community/community_paper1.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageFour: file(relativePath: { eq: "community/community_paper2.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageFive: file(relativePath: { eq: "community/community_mockup.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
  <Layout>
    <Container fluid={true}>
      <Row>
        <ProjectNav 
          title="Community — UX Designer"
          info="Fall 2018 | Pittsurgh, PA"
          skills="User research, Affinity diagramming, Wireframing, User testing, Sketch"
          left="/apple/"
          right="/fullstackdev/"
        >
        </ProjectNav>
        <Anime opacity={[0, 1]} translateY={'1em'} delay={(e, i) => 250}>
        <Col xs={12} sm={12} md={12} lg={{span:8, offset:4}} xl={{span:8, offset:4}} className="project-content">
          <Img fluid={data.imageOne.childImageSharp.fluid} alt="project cover"/>
          <br /><br />
          <h2 className="project-h2">Background</h2>
          <p>When it comes to course help, professors have many different methods of providing additional help to students, whether they have a team of teaching assistants (TA) to hold in-person office hours or use an online tool to answer students’ questions. Courses with 100+ students can become difficult to manage when handling student questions outside of the classroom. During this project, I worked alongside with <a href="https://armanh.com/" target="_blank" className="link">Arman Hezarkhani</a>, <a href="https://www.tabb.me/" target="_blank" className="link">Taylor Tabb</a> and <a href="https://www.linkedin.com/in/olivia-weiss-73b486111/" target="_blank" className="link">Olivia Weiss</a> to explore this problem domain, where I served as the UX researcher and designer.</p>
          <br />

          <h2 className="project-h2">Initial Research</h2>
          <p>I looked into existing online Q/A platforms and conducted several initial interviews in order to learn a bit more about the current pain points for instructors when communicating with their students and students in need of help during the course.</p>
          <br />

          <h2 className="project-h2">Interviews and Affinity Diagramming</h2>
          <p>After gathering initial knowledge, I conducted interviews with professors, TAs and students. One goal from these interviews was to learn more specifically about Piazza and its use in the classroom. I then synthesized the interview data with affinity diagramming in order to gather key findings.</p>
          <Img fluid={data.imageTwo.childImageSharp.fluid} alt="two males affinity diagram" /><br />
          <p>Several of my findings fell under the idea of student empowerment. There are pros and cons when it comes to using online Q/A tools for a course. For example:</p>
          <p><b>Pro:</b> In-person course help is limited and inefficient. Students have a place where they can get help immediately</p>
          <p><b>Con:</b> Some prefer more personal, face to face communication to get deeper help on questions</p>
          <br />

          <h2 className="project-h2">Paper Mockups</h2>
          <p>Before creating any high-fi mockups, I created paper mockups to test three main features: 1) Testing filtering and tagging 2) Testing the following feature 3) Testing the thread feature (these features were determined based on my research and key findings).</p>

          <Img fluid={data.imageThree.childImageSharp.fluid} alt="paper mockup"/><br />
          <Img fluid={data.imageFour.childImageSharp.fluid} alt="paper mockup 2"/><br />

          <p>After testing my paper mockups, I got feedback on certain interactions and the general layout. Testing gave me insight on how the current features should be changed in order to improve the interaction flows.</p>

          <h2 className="project-h2">Designs</h2>
          <p>While access to see all designs is <i>confidential</i> (as this project is currently under development), here’s an example that shows part of one of my designs:</p>
          <Img fluid={data.imageFive.childImageSharp.fluid} alt="wireframe showing posts"/><br />
          <p>This example shows a list view of posts (some of which are questions asked by students or announcements made by instructors). Each post is tagged and is marked as either <i>resolved</i> and <i>unresolved</i>. Feel free to <a href="mailto:cchang2@alumni.cmu.edu" className="link">contact me</a> to learn more!</p>

        </Col>
        </Anime>
      </Row>
    </Container>
  </Layout>
  )
}

export default CommunityPage
