import React from "react"
import Anime from 'react-anime';
import { useStaticQuery, Link, graphql } from "gatsby"
import Img from "gatsby-image"
import PropTypes from "prop-types"
import 'bootstrap/dist/css/bootstrap.css';
import { Container, Row, Col, Navbar, NavDropdown, Nav } from "react-bootstrap"
import "./app.css"

const ProjectNav = (props) => {
  
  const skillItems = props.skills.split(", ").map( (skill) => <li>{skill}</li> )
  const data = useStaticQuery(graphql`
    query {
      imageOne: file(relativePath: { eq: "icons/home_black.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageTwo: file(relativePath: { eq: "icons/left_black.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageThree: file(relativePath: { eq: "icons/right_black.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <nav aria-label="Project Subnavigation"> 
      <Col xs={12} sm={12} md={12} lg={3} xl={3} className="project-sidebar" id="project-sidebar">
        <Anime opacity={[0, 1]} translateY={'1em'} delay={(e, i) => 250}>
          <h1 className="project-h">{props.title}</h1>
          <p className="project-info">{props.info}</p>
          <br />
          <ul className="project-skills">
            {skillItems}
          </ul>
        </Anime>
        <Anime opacity={[0, 1]} translateY={'1em'} delay={(e, i) => 500}>
        <br />< br />
        <div>
          <ul className="project-sidebar-linklist">
            <li><a href="/"><Img fluid={data.imageOne.childImageSharp.fluid} style={{ width:`22px` }} className="project-sidebar-link" /></a></li>
            <li><a href={props.left}><Img fluid={data.imageTwo.childImageSharp.fluid} style={{ width:`13px` }} className="project-sidebar-link" /></a></li>
            <li><a href={props.right}><Img fluid={data.imageThree.childImageSharp.fluid} style={{ width:`13px` }} className="project-sidebar-link" /></a></li>
          </ul>
        </div>
        </Anime>
      </Col>
    </nav>
  )
}

export default ProjectNav